<template>
  <div class="search">
    <van-nav-bar fixed title="商品列表" left-arrow @click-left="$router.go(-1)" />
    <!-- 搜索栏 -->
    <van-search
      readonly
      shape="round"
      background="#ffffff"
      :value="querySearch || '搜索商品'"
      show-action
      @click="$router.push('/search')"
      >
      <template #action>
        <van-icon class="tool" name="apps-o" />
      </template>
    </van-search>
    <!-- 排序选项按钮 -->
    <div class="sort-btns">
      <div v-for='item in typeList' :key='item' @click='getProListWithType(item)' :class="{ active: sortType === item, 'sort-item': true }">{{ item }}</div>
    </div>
    <!-- 商品列表 -->
    <div class="goods-list">
      <GoodsItem v-for="item in proList" :key="item.goods_id" :item='item'></GoodsItem>
    </div>
  </div>
</template>

<script>
import { getProList } from '@/api/product'
import GoodsItem from '@/components/goodsItem'
export default {
  name: 'SearchList',
  components: {
    GoodsItem
  },
  data () {
    return {
      typeList: ['综合', '销量', '价格'], // 排序类型列表
      sortType: '', // 排序类型
      page: 1, // 页码
      proList: [] // 商品列表
    }
  },
  computed: {
    querySearch () {
      return this.$route.query.search
    }
  },
  methods: {
    // 根据排序类型获得商品列表
    async getProListWithType (type) {
      // 更改排序类型
      this.sortType = type
      // 获取商品数据
      const { data: { list } } = await getProList({
        sortType: this.sortType,
        categoryId: this.$route.query.categoryId,
        goodsName: this.querySearch,
        page: this.page
      })
      // 更新商品列表
      this.proList = list.data
    }
  },
  created () {
    this.getProListWithType('综合')
  }
}
</script>

<style lang="less" scoped>
.search {
  padding-top: 46px;
  ::v-deep .van-icon-arrow-left {
    color: #333;
  }
  .tool {
    font-size: 24px;
    height: 40px;
    line-height: 40px;
  }
  .sort-btns {
    display: flex;
    height: 36px;
    line-height: 36px;
    .sort-item {
      text-align: center;
      flex: 1;
      font-size: 16px;
    }
    .active {
      color: red;
    }
  }
}

// 商品样式
.goods-list {
  background-color: #f6f6f6;
}
</style>
